export default {
	props:{
		tablename:"",
		objectid:""
	},
	data:function(){
		return {
			cmData:{},
			cmFormClass:"",
			cmBtnClass:""
		}
	},
	created:function(){
		this.getList();
	},
	methods:{
		getList:function(){
			var that=this;
			uni.request({
				url:that.app.apiHost+"?fromapp=wxapp&m=comment&ajax=1&tablename="+this.tablename+"&objectid="+this.objectid,
				success:function(res){
					that.cmData=res.data.data;
				}
			})
		},
		cmFormShow:function(){
			console.log("show");
			this.cmBtnClass="none";
			this.cmFormClass="flex-col";
		},
		cmFormHide:function(){
			console.log("hide");
			this.cmBtnClass="";
			this.cmFormClass="";
		},
		cmFormSubmit:function(e){
			var that=this;
			uni.request({
				url:that.app.apiHost+"?fromapp=wxapp&m=comment&a=save&ajax=1&authcode="+that.app.getAuthCode(),
				data:e.detail.value,
				method:"POST",
				header:{
					"content-type":"application/x-www-form-urlencoded"
				},
				success:function(res){
					console.log(res.data);
					uni.showToast({
						title: res.data.message,
						duration: 2000
					});
					that.cmBtnClass="";
					that.cmFormClass="";
				}
			})
		}
	}
}
